.wrapper {
  display: block;
  width: 500px;
  background-color: white;
  margin-top: 50px;
  padding: 30px 30px;
  justify-content: center;

  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
}
.list  {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 10px;
}
.button {
  display: inline-block;
  text-decoration: none;
  padding: 12px;
  font-weight: 700;
  color: #2a363b;
  margin-top: 20px;
  margin-bottom: 20px;

}
.buttonWrapper {
  display: block;
  display: flex;
  flex-direction: column;
  padding: 10px 20px;
  justify-content: left;
}

.title {
  color: black;
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 1.19;
  text-align: left;
  letter-spacing: 0.03em;
  margin-bottom: 10px;
}

.btn {
  display: inline-block;
  border: none;
  border-radius: 4px;
  padding: 10px 32px;
  width: 150px;
  cursor: pointer;
  margin: 5px 5px;
}

.btnDel {
  display: inline-block;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
  width: 80px;
  cursor: pointer;
  margin: 5px 5px;
}

.contact {
  display: flex;
  justify-content: space-between;
}

.text {
  display: flex;
  flex-direction: column;
  margin: 5px 5px;

  color: gray;
  font-size: 18px;
  line-height: 1.19;
  text-align: left;
  letter-spacing: 0.03em;
}
